<template>
  <div>
    <div class="topTitle">
      <span class="topTitle-span1">统计分析</span>
      <span class="topTitle-span1">
        <img src="@/assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">客户信息统计</span>
    </div>
    <div class="contentCellBox">
      <Tabs @on-click="reset">
        <TabPane label="期末客户总数等指标-时点统计表" name="name1">
          <div class="export-content">
              <Button type="primary"  @click="exportFn" v-authcontrol="{menuCode:'109001001000'}">
                <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
              </Button>
          </div>
          <div class="contentCellBox">
            <div class="contentCellTitle">
              <div class="contentCellTitle-1"></div>查询条件
            </div>
            <div>
              <Form
                ref="formValidate"
                :model="formValidate"
                :rules="ruleValidate"
                :label-width="120"
                inline
              >
                <Row>
                  <FormItem label="统计日期：" prop="date1">
                      <DatePicker split-panels type="date" :value="formValidate.date1" :options="options1" @on-change="formValidate.date1=$event"></DatePicker>
                  </FormItem>
                </Row>
                <Row>
                  <div class="btnClass">
                    <Button type="primary" @click="handleSubmit('1')" style="width:89px">查询</Button>
                  </div>
                </Row>
              </Form>
            </div>
          </div>
          <div class="contentCellBox">
              <div class="contentCellTitle">
                <div class="contentCellTitle-1"></div>期末客户总数等指标-时点统计表
              </div>
              <div class="contentCellBody">
                <Row>
                  <table  border="1"   cellpadding="2" class="tableBox2">
                    <thead>
                      <tr align="center">
                          <td rowspan="2">序</td>
                          <td rowspan="2">业务条线</td>
                          <td colspan="4">个人客户</td>
                          <td colspan="4">机构客户</td>
                          <td colspan="4">产品客户</td>
                      </tr>
                      <tr>
                          <td>客户总数</td>
                          <td>缺项客户数</td>
                          <td>证件将到期客户数</td>
                          <td>证件已到期客户数</td>			
                          <td>客户总数</td>
                          <td>缺项客户数</td>
                          <td>证件将到期客户数</td>
                          <td>证件已到期客户数</td>			
                          <td>客户总数</td>
                          <td>缺项客户数</td>
                          <td>证件将到期客户数</td>
                          <td>证件已到期客户数</td>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item,index) in tableBody">
                        <td>{{index+1}}</td>
                        <td>{{item.mbrc}}</td>
                        <td>{{item.allCntPer}}</td>
                        <td>{{item.missitemsCntPer}}</td>
                        <td>{{item.certwarn00CntPer}}</td>
                        <td>{{item.certwarn01CntPer}}</td>
                        <td>{{item.allCntOrg}}</td>
                        <td>{{item.missitemsCntOrg}}</td>
                        <td>{{item.certwarn00CntOrg}}</td>
                        <td>{{item.certwarn01CntOrg}}</td>
                        <td>{{item.allCntPro}}</td>
                        <td>{{item.missitemsCntPro}}</td>
                        <td>{{item.certwarn00CntPro}}</td>
                        <td>{{item.certwarn01CntPro}}</td>
                      </tr>
                    </tbody>
                  </table>
                </Row>
              </div>
          </div>
        </TabPane>
        <TabPane label="新增客户数、销户客户数-时段统计表" name="name2">
          <div class="contentCellBox">
              <div class="contentCellTitle">
                <div class="contentCellTitle-1"></div>查询条件
                <div class="export-content">
                    <Button type="primary"  @click="exportFn" v-authcontrol="{menuCode:'109001002000'}">
                      <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
                    </Button>
                </div>
              </div>
              <div>
                <Form
                  ref="formValidate2"
                  :model="formValidate2"
                  :rules="ruleValidate"
                  :label-width="120"
                  inline
                >
                  <Row>
                    <FormItem label="统计日期：" prop="date2">
                        <DatePicker split-panels type="daterange" :value="formValidate2.date2" :options="options1" @on-change="formValidate2.date2=$event"></DatePicker>
                    </FormItem>
                  </Row>
                  <Row>
                    <div class="btnClass">
                      <Button type="primary" @click="handleSubmit('2')" style="width:89px">查询</Button>
                    </div>
                  </Row>
                </Form>
              </div>
          </div>
          <div class="contentCellBox">
              <div class="contentCellTitle">
                <div class="contentCellTitle-1"></div>新增客户数、销户客户数-时段统计表
              </div>
              <div class="contentCellBody">
                <Row>
                  <table  border="1"   cellpadding="2" class="tableBox2">
                    <thead>
                      <tr align="center">
                          <td rowspan="2">序</td>
                          <td rowspan="2">业务条线</td>
                          <td colspan="2">个人客户</td>
                          <td colspan="2">机构客户</td>
                          <td colspan="2">产品客户</td>
                      </tr>
                      <tr>
                        <td>新增客户数</td>
                        <td>销户客户数</td>
                        <td>新增客户数</td>
                        <td>销户客户数</td>
                        <td>新增客户数</td>
                        <td>销户客户数</td>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in tableBody2">
                        <td>{{index+1}}</td>
                        <td>{{item.mbrc}}</td>
                        <td>{{item.addCntPer}}</td>
                        <td>{{item.clsCntPer}}</td>
                        <td>{{item.addCntOrg}}</td>
                        <td>{{item.clsCntOrg}}</td>
                        <td>{{item.addCntPro}}</td>
                        <td>{{item.clsCntPro}}</td>
                      </tr>
                    </tbody>
                  </table>
                </Row>
              </div>
          </div>
        </TabPane>
      </Tabs>
    </div>
  </div>
</template>
<script>
export default {
  created() {
      this.formValidate.date1 = this.getFormatDate();
      this.formValidate2.date2 = [this.getFormatDate(), this.getFormatDate()];
      this.axios
      .get(`/stat/kyc/getKycStatInfo?stdt=${this.formValidate.date1}`)
      .then((response)=> {
            if (response.code=="200"){
                this.tableBody = response.data;
            }
      })
      .catch(function(error) {});
  },
  data() {
    return {
      formValidate: {
        date1: ""
      },
      name: "name1",
      formValidate2: {
        date2: ["",""],
      },
      tableBody: [
      ],
      tableBody2: [
      ],
      options1: {
          disabledDate (date) {
              return date && date.valueOf() > Date.now()-24*60*60*1000;
          }
      },
      ruleValidate: {
          date1: [
            {
              required: true,
              validator: (rule, value, callback) => {
                if (this.formValidate.date1 == "") {
                  return callback(new Error("请选择时间"));
                } else {
                  callback();
                }
              },
              trigger: "on-change"
            }
        ],
        date2: [
            {
              required: true,
              validator: (rule, value, callback) => {
                if (this.formValidate2.date2[0] == "") {
                  return callback(new Error("请选择时间"));
                } else {
                  callback();
                }
              },
              trigger: "on-change"
            }
        ],
      }
    };
  },
  methods: {
    handleSubmit(type){
      let valid1 = "";
      this.$refs[type == "1"?"formValidate":"formValidate2"].validate(valid => {
          valid1 = valid;
      });
      if (!valid1)  return;
      if (type == "1"){
        this.axios
        .get(`/stat/kyc/getKycStatInfo?stdt=${this.formValidate.date1}`)
        .then((response)=> {
              if (response.code=="200"){
                  this.tableBody = response.data;
              }
        })
        .catch(function(error) {});
      }else {
        let data = {
          start: this.formValidate2.date2[0],
          end: this.formValidate2.date2[1]
        };
        data = JSON.stringify(data);
        this.axios
          .post("/stat/kyc/getKycNumStat", data, {
            headers: { "Content-Type": "application/json" }
          })
          .then(response => {
            if (response.code=="200"){
                  if (response.data.length == 0){
                    this.$Message.info("请稍后尝试查询，正在执行中");
                  }
                  this.tableBody2 = response.data;
            }
          })
          .catch(error => {});
      }
    },
    reset(name) {
      this.name = name;
      if (name == "name1"){
          this.tableBody2 = [];
          this.axios
          .get(`/stat/kyc/getKycStatInfo?stdt=${this.formValidate.date1}`)
          .then((response)=> {
                if (response.code=="200"){
                    this.tableBody = response.data;
                }
          })
          .catch(function(error) {});
      }else{
        this.tableBody = [];
        // let data = {
        //   start: this.formValidate2.date2[0],
        //   end: this.formValidate2.date2[1]
        // };
        // data = JSON.stringify(data);
        // this.axios
        //   .post("/stat/kyc/getKycNumStat", data, {
        //     headers: { "Content-Type": "application/json" }
        //   })
        //   .then(response => {
        //     if (response.code=="200"){
        //           if (response.data.length == 0){
        //               this.$Message.info("请稍后尝试查询，正在执行中");
        //           }
        //           this.tableBody2 = response.data;
        //     }
        //   })
        //   .catch(error => {});
      }
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    exportFn(){
      if (this.name == "name1"){
          if (this.tableBody.length == 0) {
            this.$Message.warning("暂无数据");
            return;
          }
          this.axios
          .get(`/stat/kyc/doExportKycStatInfo?stdt=${this.formValidate.date1}`,{responseType: "blob"})
          .then(response=> {
                this.download(response,'导出文档.xls');
          })
          .catch(function(error) {});
      }else{
        if (this.tableBody2.length == 0) {
            this.$Message.warning("暂无数据");
            return;
        }
        let data = {
          start: this.formValidate2.date2[0],
          end: this.formValidate2.date2[1]
        };
        data = JSON.stringify(data);
        this.axios
          .post("/stat/kyc/doExportKycNumStat", data, {
            headers: { "Content-Type": "application/json" },
            responseType: "blob"
          })
          .then(response => {
              this.download(response,'导出文档.xls');
          })
          .catch(error => {});
      }
    },
    getFormatDate() {
        var date = new Date(new Date()-24*60*60*1000);
        var seperator1 = "-";
        var year = date.getFullYear();
        var month = date.getMonth() + 1;
        var strDate = date.getDate();
        if (month >= 1 && month <= 9) {
            month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
            strDate = "0" + strDate;
        }
        var currentdate = year + seperator1 + month + seperator1 + strDate;
        return currentdate;
    }
  },
  watch: {}
};
</script>
<style lang="scss">
</style>


